import { Injectable } from '@angular/core';

declare var navigator;

@Injectable()
export class BleConnectivityProvider {

  constructor() {

  }

  findNearbyDevices(): Promise<any> {
    if (navigator.bluetooth) {
      return navigator.bluetooth.requestDevice({
        acceptAllDevices: true,
        optionalServices: ['battery_service']
      });
    }
    return Promise.reject("Bluetooth is Not Available");
  }

  connectToDevice(device): Promise<any> {
    return device.gatt.connect();
  }

}

export interface BluetoothDevice {
  id: string;
  name: string;
  gatt: BluetoothRemoteGATTServer;
  uuids: any[];
};

export interface BluetoothRemoteGATTServer {
  device: BluetoothDevice;
  connected: boolean;
  connect(): Promise<BluetoothRemoteGATTServer>;
  disconnect();
  getPrimaryService(any): Promise<any>;
  getPrimaryServices(any): Promise<any>;
};




