import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ItemsPage } from '../pages/items/items';
import { CardInOutComponent } from '../components/card-in-out/card-in-out';
import { ChunkPipe } from '../pipes/chunk/chunk';
import { NgxZxingModule } from '@zxing/ngx-scanner';
import { QrCodeReaderPage } from '../pages/qr-code-reader/qr-code-reader';
// import { WebBluetoothModule } from '@manekinekko/angular-web-bluetooth';
import { BleConnectivityProvider } from '../providers/ble-connectivity/ble-connectivity';
import { BluetoothConnectPage } from '../pages/bluetooth-connect/bluetooth-connect';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ItemsPage,
    CardInOutComponent,
    ChunkPipe,
    QrCodeReaderPage,
    BluetoothConnectPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    NgxZxingModule.forRoot(),
    // WebBluetoothModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ItemsPage,
    CardInOutComponent,
    QrCodeReaderPage,
    BluetoothConnectPage
  ],
  providers: [
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    BleConnectivityProvider
  ]
})
export class AppModule { }



