import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BleConnectivityProvider, BluetoothDevice } from '../../providers/ble-connectivity/ble-connectivity';
// import { BluetoothCore } from '@manekinekko/angular-web-bluetooth'

@Component({
  selector: 'page-bluetooth-connect',
  templateUrl: 'bluetooth-connect.html',
  // providers: [  BluetoothCore ]  
})
export class BluetoothConnectPage {

  private info: string = "Log: ";

  constructor(public navCtrl: NavController, public navParams: NavParams, private bleConnect: BleConnectivityProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BluetoothConnectPage');
  }

  startScan() {
    this.bleConnect.findNearbyDevices()
      .then((device: BluetoothDevice) => {
        this.info += "<br>" + "Device Name: " + device.name + " Id: " + device.id + " Uuids: " + device.uuids.join(",");
        alert("Device Name: " + device.name);
      })
      .catch((err) => {
        this.info += "<br>" + err;
      })
  }
}



