import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { QrCodeReaderPage } from '../qr-code-reader/qr-code-reader';
import { BluetoothConnectPage } from '../bluetooth-connect/bluetooth-connect';

@Component({
  selector: 'page-items',
  templateUrl: 'items.html',
})
export class ItemsPage {

  private subPages: Array<{ title: string, component: any }> = [
    { title: 'My Wallet', component: "" },
    { title: 'Games', component: "" },
    { title: 'Rewards', component: "" },
    { title: 'Game Reserve', component: "" },
    { title: 'My Offers', component: "" },
    { title: 'Beverage', component: "" },
    { title: 'My Calendar', component: "" },
    { title: 'Wayfinder', component: "" },
    { title: 'Property Info', component: "" },
    { title: 'Call Attendant', component: "" },  // changed to caller 
    { title: 'Test Page', component: "" }
  ]
  // new slider content
  private slidesImage: Array<{ image: string }> = [   // default images
    {
      image: "assets/img/dark/banner-a.png",
    },
    {
      image: "assets/img/dark/banner-1.png",
    },
    {
      image: "assets/img/dark/banner-2.png",
    }
  ];

  currentUser: any = {};

  walletCash: any ={};
  walletPoints: any = {};
  walletPromo: any = {};
  bubbleBurstOffer = {};
  rewardsQuestOffer = {};

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  openQr() {
    this.navCtrl.setRoot(QrCodeReaderPage);
  }

  openBle() {
    this.navCtrl.setRoot(BluetoothConnectPage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ItemsPage');
  }

}



