import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';

@Component({
  selector: 'page-qr-code-reader',
  templateUrl: 'qr-code-reader.html',
})
export class QrCodeReaderPage {

  selectedDevice: ICamera;
  camStarted: boolean = false;
  availableDevices: ICamera[] = [];
  consoleLog: string = "";
  currentIndex: number = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad QrCodeReaderPage');
  }

  displayCameras(cameras: ICamera[]) {
    debugger;
    if (cameras && cameras.length) {
      this.consoleLog += "Available Options" + "<br>";
      cameras.forEach(element => {
        this.consoleLog += element.deviceId + " - " + element.label + "<br>";
      });

      this.currentIndex = 0;
      this.availableDevices = cameras;
      this.selectedDevice = cameras[0];
      this.camStarted = true;
    } else {
      this.consoleLog += "No Cameras" + "<br>";
    }
  }

  handleQrCodeResult(result) {
    if (result) {
      this.consoleLog += result + "<br>";
      alert(result);
    }
  }

  onChange(device: ICamera) {
    this.consoleLog += device.label + "<br>";
    this.selectedDevice = device;
    this.camStarted = true;
  }

  swap() {
    let total: number = this.availableDevices.length;
    if (total >= 2) {
      let curr: number = this.currentIndex;
      let swapped: number = (total - curr + 1) % total;
      
      this.currentIndex = swapped;
      this.consoleLog += swapped + "<br>";
      this.selectedDevice = this.availableDevices[swapped];
    }
  }
}

export interface ICamera {
  deviceId: string;
  groupId: string;
  kind: string;
  label: string;
}



